.rawposters {
  color: white;
  margin-top: 2rem;
  margin-left: 20px;
}
.posters {
  display: flex;
  padding: 20px;
  overflow-x: scroll;
  overflow-y: hidden;
}
.posters::-webkit-scrollbar {
  display: none;
}
.poster {
  max-height: 500px;
  max-width: 400px;
  margin-right: 1.5rem;
  cursor: pointer;
}
.smallposter {
  max-height: 400px;
  max-width: 300px;
  margin-right: 1rem;
  cursor: pointer;
}
.poster :hover {
  transform: scale(1.4);
}
.smallposter:hover {
  transform: scale(1.4);
}
